<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <h1>Free search</h1>
        <p>*Tracking number search</p>
        <form @submit="analyze">
          <label for="records1">Records to analyze</label>
          <textarea
            name="records1"
            id="records1"
            class="form-control"
            cols="30"
            rows="10"
            v-model="records"
            required
          ></textarea>
          <input class="btn btn-primary" type="submit" value="Submit" />
        </form>
      </div>
      <div class="col">
        <h1>Label search</h1>
        <p>*Label search</p>
        <form @submit="analyzelabel">
          <label for="records2">Label to analyze</label>
          <select
            name="records2"
            id="records2"
            class="form-control"
            v-model="label"
            required
          >
            <option :key="a.id" :value="a.id" v-for="a in grouplabels">
              {{ a.label }}
            </option>
          </select>
          <input class="btn btn-primary" type="submit" value="Submit" />
        </form>
      </div>
    </div>
    <div class="row" v-if="display">
      <div class="col">
        <h1>Result</h1>
        <div id="accordion">
          <div class="card text-white bg-dark">
            <div class="card-header" id="headingOne">
              <h5 class="mb-0">
                <button
                  class="btn btn-link"
                  data-toggle="collapse"
                  data-target="#collapseOne"
                  aria-expanded="true"
                  aria-controls="collapseOne"
                >
                  Summary
                </button>
              </h5>
            </div>

            <div
              id="collapseOne"
              class="collapse show"
              aria-labelledby="headingOne"
              data-parent="#accordion"
            >
              <div class="card-body">
                <table class="table table-dark table-striped">
                  <thead>
                    <tr>
                      <th>Delivered</th>
                      <th>Shipped</th>
                      <th>Returned</th>
                      <th>Lost</th>
                      <th>Total</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td class="delivered">
                        {{ display.summary.delivered }} ({{
                          Math.round(
                            (10000 * display.summary.delivered) /
                              display.summary.total_records
                          ) / 100
                        }}%)
                      </td>
                      <td class="delayed">
                        {{ display.summary.shipped }} ({{
                          Math.round(
                            (10000 * display.summary.shipped) /
                              display.summary.total_records
                          ) / 100
                        }}%)
                      </td>
                      <td class="returned">
                        {{ display.summary.returned }} ({{
                          Math.round(
                            (10000 * display.summary.returned) /
                              display.summary.total_records
                          ) / 100
                        }}%)
                      </td>
                      <td class="lost">
                        {{ display.summary.lost }} ({{
                          Math.round(
                            (10000 * display.summary.lost) /
                              display.summary.total_records
                          ) / 100
                        }}%)
                      </td>
                      <td>{{ display.summary.total_records }}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <div class="card text-white bg-dark">
            <div class="card-header" id="headingTwo">
              <h5 class="mb-0">
                <button
                  class="btn btn-link collapsed"
                  data-toggle="collapse"
                  data-target="#collapseTwo"
                  aria-expanded="false"
                  aria-controls="collapseTwo"
                >
                  Times
                </button>
              </h5>
            </div>
            <div
              id="collapseTwo"
              class="collapse"
              aria-labelledby="headingTwo"
              data-parent="#accordion"
            >
              <div class="card-body">
                <table class="table table-dark table-striped">
                  <thead>
                    <tr>
                      <th>Delivered<br />Avg. days</th>
                      <th>Overall<br />Avg. days</th>
                      <th>In shipment<br />Avg. days</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>
                        {{
                          Math.round(
                            display.times.delivered.totaltime_ms /
                              display.times.delivered.number /
                              100 /*ms +10 after rounding */ /
                              60 /*sec*/ /
                              60 /*min*/ /
                              24 /*hr*/
                          ) / 10
                        }}
                      </td>
                      <td>
                        {{
                          Math.round(
                            (display.times.delivered.totaltime_ms +
                              display.times.inshipment.totaltime_ms) /
                              (display.times.delivered.number +
                                display.times.inshipment.number) /
                              100 /*ms +10 after rounding */ /
                              60 /*sec*/ /
                              60 /*min*/ /
                              24 /*hr*/
                          ) / 10
                        }}
                      </td>
                      <td>
                        {{
                          Math.round(
                            display.times.inshipment.totaltime_ms /
                              display.times.inshipment.number /
                              100 /*ms +10 after rounding */ /
                              60 /*sec*/ /
                              60 /*min*/ /
                              24 /*hr*/
                          ) / 10
                        }}
                      </td>
                    </tr>
                  </tbody>
                </table>
                <div id="graph_area"></div>
              </div>
            </div>
          </div>
          <div class="card text-white bg-dark">
            <div class="card-header" id="headingThree">
              <h5 class="mb-0">
                <button
                  class="btn btn-link collapsed"
                  data-toggle="collapse"
                  data-target="#collapseThree"
                  aria-expanded="false"
                  aria-controls="collapseThree"
                >
                  Status
                </button>
              </h5>
            </div>
            <div
              id="collapseThree"
              class="collapse"
              aria-labelledby="headingThree"
              data-parent="#accordion"
            >
              <div class="card-body">
                <h2>Delivered statuses</h2>
                <table class="table table-dark table-striped">
                  <thead>
                    <tr>
                      <th>Front door delivery</th>
                      <th>Post box delivery</th>
                      <th>In person delivery</th>
                      <th>Unknown delivery</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>{{ display.status.delivered.door }}</td>
                      <td>{{ display.status.delivered.post_locker }}</td>
                      <td>{{ display.status.delivered.reception_person }}</td>
                      <td>{{ display.status.delivered.unknown }}</td>
                    </tr>
                  </tbody>
                </table>
                <h2>In shipment statuses</h2>
                <table class="table table-dark table-striped">
                  <thead>
                    <tr>
                      <th>Prepare shipment</th>
                      <th>In transit</th>
                      <th>At post office</th>
                      <th>Other</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>{{ display.status.inshipment.prepare_shipping }}</td>
                      <td>{{ display.status.inshipment.in_tansit }}</td>
                      <td>
                        {{
                          display.status.inshipment
                            .delivery_attempt_await_pickup
                        }}
                      </td>
                      <td>{{ display.status.inshipment.other }}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import * as d3 from "d3";

export default {
  name: "Analyze",
  data() {
    return {
      records: "",
      label: "",
      display: undefined,
    };
  },
  computed: mapGetters(["allTrackingData", "grouplabels"]),
  methods: {
    drawgraph: function () {
      // Clear previous graph
      if (document.getElementById("graph_area")) {
        document.getElementById("graph_area").innerHTML = "";
      }

      /*
      Data source X: this.display.times.delivered.histogram.date_ts // timestamp in ms
      Data source Y: this.display.times.delivered.histogram.count   // integer count
      */
      // SVG stuff
      // set the dimensions and margins of the graph
      var margin = { top: 20, right: 20, bottom: 30, left: 40 },
        width = 960 - margin.left - margin.right,
        height = 500 - margin.top - margin.bottom;

      // set the ranges
      var x = d3
        .scaleTime()
        .domain([
          new Date(d3.min(this.display.times.delivered.histogram.date_ts)),
          new Date(d3.max(this.display.times.delivered.histogram.date_ts)),
        ])
        .range([0, width]);
      var y = d3
        .scaleLinear()
        .domain([0, d3.max(this.display.times.delivered.histogram.count)])
        .range([height, 0]);

      // append the svg object to the body of the page
      // append a 'group' element to 'svg'
      // moves the 'group' element to the top left margin
      var svg = d3
        .select("#graph_area")
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

      // append the rectangles for the bar chart
      svg
        .selectAll(".histbar")
        .data(this.display.times.delivered.histogram.date_ts)
        .enter()
        .append("rect")
        .attr("class", "histbar")
        .attr("fill", "steelblue")
        .attr("x", (d) => x(d))
        .attr("width", 10)
        .attr("y", (d, i) => y(this.display.times.delivered.histogram.count[i]))
        .attr(
          "height",
          (d, i) => height - y(this.display.times.delivered.histogram.count[i])
        );

      // add the x Axis
      svg
        .append("g")
        .attr("transform", "translate(0," + height + ")")
        .call(d3.axisBottom(x));

      // add the y Axis
      svg.append("g").call(d3.axisLeft(y));
    },
    analyze(e) {
      e.preventDefault();

      // Format data to send (from "records")
      const input_data = this.records.split(/[\r\n]+/); // Split on new line characters

      // Acquire records
      const analyze_data = this.allTrackingData.filter(
        (d) => input_data.indexOf(d.tracking) >= 0
      );

      const summary = {
        delivered: 0,
        shipped: 0,
        returned: 0,
        lost: 0,
        total_records: analyze_data.length,
      };
      const times = {
        delivered: {
          number: 0,
          totaltime_ms: 0,
          histogram: {
            date_ts: [],
            count: [],
          },
        },
        inshipment: {
          number: 0,
          totaltime_ms: 0,
        },
      };
      const status = {
        delivered: {
          door: 0,
          post_locker: 0,
          reception_person: 0,
          unknown: 0,
        },
        inshipment: {
          prepare_shipping: 0,
          in_tansit: 0,
          delivery_attempt_await_pickup: 0,
          other: 0,
        },
      };

      analyze_data.forEach((d) => {
        if (d.delivered) {
          if (d.delivereddate == 0) {
            if (d.status == "returned") {
              summary.returned++;
            } else {
              summary.lost++;
            }
          } else {
            summary.delivered++;
            if (d.delivereddate > 1) {
              times.delivered.number++;
              times.delivered.totaltime_ms += d.delivereddate - d.shippeddate;

              const date = new Date(d.delivereddate);
              const d_ts = new Date(
                date.getFullYear(),
                date.getMonth(),
                date.getDate()
              ).getTime();
              const index = times.delivered.histogram.date_ts.indexOf(d_ts);
              if (index == -1) {
                times.delivered.histogram.date_ts.push(d_ts);
                times.delivered.histogram.count.push(1);
              } else {
                times.delivered.histogram.count[index]++;
              }
            }

            // Status check
            if (
              d.status.indexOf("door") >= 0 ||
              d.status.indexOf("garage") >= 0
            ) {
              status.delivered.door++;
            } else if (
              d.status.indexOf("locker") >= 0 ||
              d.status.indexOf("mailbox") >= 0 ||
              d.status.indexOf("PO Box") >= 0
            ) {
              status.delivered.post_locker++;
            } else if (
              d.status.indexOf("reception") >= 0 ||
              d.status.indexOf("neighbor") >= 0 ||
              d.status.indexOf("picked up at") >= 0 ||
              d.status.indexOf("individual") >= 0
            ) {
              status.delivered.reception_person++;
            } else {
              status.delivered.unknown++;
            }
          }
        } else {
          summary.shipped++;

          times.inshipment.number++;
          times.inshipment.totaltime_ms += Date.now() - d.shippeddate;

          // Status check
          if (
            d.status.indexOf("label has been prepared") >= 0 ||
            d.status.indexOf("shipping partner facility") >= 0
          ) {
            status.inshipment.prepare_shipping++;
          } else if (
            d.status.indexOf("has not been updated") >= 0 ||
            d.status.indexOf("on its way to the destination") >= 0 ||
            d.status.indexOf("in transit to the destination") >= 0 ||
            d.status.indexOf("in transit to the next facility") >= 0 ||
            d.status.indexOf("arrived at the hub") >= 0 ||
            d.status.indexOf("forwarded to a different") >= 0 ||
            d.status.indexOf("arrived at the Post Office") >= 0 ||
            d.status == "Shipped"
          ) {
            status.inshipment.in_tansit++;
          } else if (
            d.status.indexOf("attempted to deliver") >= 0 ||
            d.status.indexOf("delivery attempt") >= 0 ||
            d.status.indexOf("is being held at") >= 0 ||
            d.status.indexOf("ready for pickup") >= 0
          ) {
            status.inshipment.delivery_attempt_await_pickup++;
          } else {
            status.inshipment.other++;
          }
        }
      });

      // Process analysis
      this.display = { summary, times, status };

      // Display graphs
      setTimeout(this.drawgraph, 1000);
    },
    analyzelabel(e) {
      e.preventDefault();

      // Format data to send (from "label")
      const input_data = parseInt(this.label);

      // Acquire records
      const analyze_data = this.allTrackingData.filter(
        (d) => d.grouplabel === input_data
      );

      const summary = {
        delivered: 0,
        shipped: 0,
        returned: 0,
        lost: 0,
        total_records: analyze_data.length,
      };
      const times = {
        delivered: {
          number: 0,
          totaltime_ms: 0,
          histogram: {
            date_ts: [],
            count: [],
          },
        },
        inshipment: {
          number: 0,
          totaltime_ms: 0,
        },
      };
      const status = {
        delivered: {
          door: 0,
          post_locker: 0,
          reception_person: 0,
          unknown: 0,
        },
        inshipment: {
          prepare_shipping: 0,
          in_tansit: 0,
          delivery_attempt_await_pickup: 0,
          other: 0,
        },
      };

      analyze_data.forEach((d) => {
        if (d.delivered) {
          if (d.delivereddate == 0) {
            if (d.status == "returned") {
              summary.returned++;
            } else {
              summary.lost++;
            }
          } else {
            summary.delivered++;
            if (d.delivereddate > 1) {
              times.delivered.number++;
              times.delivered.totaltime_ms += d.delivereddate - d.shippeddate;

              const date = new Date(d.delivereddate);
              const d_ts = new Date(
                date.getFullYear(),
                date.getMonth(),
                date.getDate()
              ).getTime();
              const index = times.delivered.histogram.date_ts.indexOf(d_ts);
              if (index == -1) {
                times.delivered.histogram.date_ts.push(d_ts);
                times.delivered.histogram.count.push(1);
              } else {
                times.delivered.histogram.count[index]++;
              }
            }

            // Status check
            if (
              d.status.indexOf("door") >= 0 ||
              d.status.indexOf("garage") >= 0
            ) {
              status.delivered.door++;
            } else if (
              d.status.indexOf("locker") >= 0 ||
              d.status.indexOf("mailbox") >= 0 ||
              d.status.indexOf("PO Box") >= 0
            ) {
              status.delivered.post_locker++;
            } else if (
              d.status.indexOf("reception") >= 0 ||
              d.status.indexOf("neighbor") >= 0 ||
              d.status.indexOf("picked up at") >= 0 ||
              d.status.indexOf("individual") >= 0
            ) {
              status.delivered.reception_person++;
            } else {
              status.delivered.unknown++;
            }
          }
        } else {
          summary.shipped++;

          times.inshipment.number++;
          times.inshipment.totaltime_ms += Date.now() - d.shippeddate;

          // Status check
          if (
            d.status.indexOf("label has been prepared") >= 0 ||
            d.status.indexOf("shipping partner facility") >= 0
          ) {
            status.inshipment.prepare_shipping++;
          } else if (
            d.status.indexOf("has not been updated") >= 0 ||
            d.status.indexOf("on its way to the destination") >= 0 ||
            d.status.indexOf("in transit to the destination") >= 0 ||
            d.status.indexOf("in transit to the next facility") >= 0 ||
            d.status.indexOf("arrived at the hub") >= 0 ||
            d.status.indexOf("forwarded to a different") >= 0 ||
            d.status.indexOf("arrived at the Post Office") >= 0 ||
            d.status == "Shipped"
          ) {
            status.inshipment.in_tansit++;
          } else if (
            d.status.indexOf("attempted to deliver") >= 0 ||
            d.status.indexOf("delivery attempt") >= 0 ||
            d.status.indexOf("is being held at") >= 0 ||
            d.status.indexOf("ready for pickup") >= 0
          ) {
            status.inshipment.delivery_attempt_await_pickup++;
          } else {
            status.inshipment.other++;
          }
        }
      });

      // Process analysis
      this.display = { summary, times, status };

      // Display graphs
      setTimeout(this.drawgraph, 1000);
    },
  },
};
</script>

<style scoped>
.delivered {
  color: rgb(199, 255, 143);
}
.delayed {
  color: rgb(169, 203, 241);
}
.returned {
  color: rgb(250, 214, 115);
}
.lost {
  color: rgb(255, 97, 97);
}
</style>
